import React from "react";

function selectVid(videoIdObj, onVidSelect) {
  onVidSelect(videoIdObj.vidId);
}

function getCss(imageurl) {
  const styles = {
    backgroundImage: `url(${imageurl})`,
    backgroundSize: "cover",
    backgroundPosition: "center center",
    height: "180px",
    position: "relative"
  };
  return styles;
}

function constructVideoTitles(vidData, onVidSelect) {
  return vidData.map(({ snippet, id }, index) => {
    return (
      <div className="video" onClick={() => selectVid(id.onVidSelect)}>
        <div style={getCss(snippet.thumbnail.high.url)} key={index}></div>
        <p className="title">{snippet.title}</p>
      </div>
    );
  });
}

const Video = ({ data, onVidSelect }) => {
  return <>{constructVideoTitles(data, onVidSelect)}</>;
};

export default Video;
