import "./styles.css";
import Search from "./components/Search";
import React from "react";
import Yt from "./Api/Yt";
import VidList from "./components/VidList";

export default class App extends React.Component {
  state = {
    vidInfo: [],
    selectedVidId: null
  };

  onSearch = async (keyword) => {
    const response = await Yt.get("/search", {
      params: {
        q: keyword
      }
    });

    console.log(response);

    this.setState({
      vidInfo: response.data.items,
      selectedVidId: response.data.items[0].id.videoId
    });
    console.log(this.state);
  };
  render() {
    return (
      <div className="App">
        <Search onSearch={this.onSearch} />
        <VidList onVidSelect={this.onVidSelect} data={this.state.vidInfo} />
      </div>
    );
  }
}
